import React from "react";
import Logo from "../../utility/Logo";
import Footer from "../Footer";

function SideBar({ content }) {
  return (
    <div className="flex items-start">
      <div className="flex flex-col justify-between px-8 mt-2 h-screen">
        <div>
          <Logo />
        </div>
        <div className="mb-auto">{content}</div>
        <div className="mt-24 bottom-0 -z-10">
          <Footer />
        </div>
      </div>
      <button className="">
        <svg width="44" height="45" viewBox="0 0 44 45" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M0 16.4111C0 7.57458 7.16344 0.411133 16 0.411133H44V44.4111H16C7.16344 44.4111 0 37.2477 0 28.4111V16.4111Z" fill="#FF5001" />
          <path d="M21 29.4111L14 22.4111L21 15.4111M29 29.4111L22 22.4111L29 15.4111" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
        </svg>
      </button>
    </div>
  );
}

export default SideBar;
