import React, { useState } from "react";

const SwitchButton = ({ title, status }) => {
  const [activ, setActive] = useState(status);
  const activhandle = () => {
    setActive(!activ);
  };

  return (
    <div>
      <button onClick={activhandle} className={activ === true ? `border border-orange-600 bg-[#FFECE4] font-semibold text-orange-600 p-2 rounded-3xl` : `border border-slate-700 bg-white font-semibold text-slate-700 p-2 rounded-3xl`}>
        {title}
      </button>
    </div>
  );
};

export default SwitchButton;
